import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { makeStyles, createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import {
  Tabs,
  Grid,
  Typography,
  Button,
  TextField,
  AppBar,
  Toolbar,
  CssBaseline
} from '@material-ui/core';
import Tab from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';
import { Container } from '@material-ui/core';
import firebase from 'firebase/app';
import 'firebase/auth';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#2196f3'
    },
    secondary: {
      main: '#f50057'
    },
    background: {
      main: '#fff'
    }
  },
  typography: {
    fontFamily: ['Lato', 'sans-serif', '-apple-system', 'BlinkMacSystemFont'].join(',')
  }
});

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper
  },
  labelStyles: {
    marginLeft: '5%',
    position: 'absolute',
    bottom: 0,
    paddingTop: '6px',
    paddingBottom: '20px'
  },
  inputContainer: {
    position: 'relative',
    marginTop: '4%'
  },
  toolbarStyle: {
    display: 'flex',
    justifyContent: 'space-between'
  }
}));

export default function SignInPage({ setAuth }) {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const [newEmail, setNewEmail] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [adminPassword, setAdminPassword] = useState('');

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const onSignIn = async () => {
    firebase
      .auth()
      .signInWithEmailAndPassword(email, password)
      .then(async function (data) {
        console.log('userSignedIn');
        return data.user.uid;
      })
      .catch(function (error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        console.log(errorMessage);
        // ...
      });
  };
  const onSignUp = async () => {
    if (newEmail === '' || newPassword === '' || adminPassword != 'ujjwal123') {
      console.log('Invalid Email Passsword or Admin Password');
      return;
    }

    try {
      await firebase
        .auth()
        .createUserWithEmailAndPassword(newEmail, newPassword)
        .then(function () {
          console.log('signed Up');
        })
        .catch(function (error) {
          // Handle Errors here.
          var errorCode = error.code;
          var errorMessage = error.message;
          console.log(errorMessage);
          // ...
        });
    } catch (error) {
      console.log(error.message);
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline>
        <Container style={{ zIndex: '999' }}>
          <AppBar position="static" BackgroundColor="primary">
            <Tabs
              value={value}
              onChange={handleChange}
              aria-label="simple tabs example"
              style={{ margin: '0 auto' }}
            >
              <Tab label="SignIn" {...a11yProps(0)} style={{ marginRight: '50px' }} />
              <Tab label="SignUp" {...a11yProps(1)} style={{ marginLeft: '50px' }} />
            </Tabs>
          </AppBar>
          <TabPanel value={value} index={0} style={{ backgroundColor: 'rgba(255,255,255,0.5)' }}>
            <Grid container spacing={1}>
              <Grid container item xs={12} spacing={3} className={classes.inputContainer}>
                <Grid item xs={4}>
                  <Typography className={classes.labelStyles}>Email</Typography>
                </Grid>
                <Grid item xs={8}>
                  <TextField
                    type="email"
                    variant="standard"
                    label="Email"
                    className={classes.inputStyles}
                    value={email}
                    fullWidth
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </Grid>
              </Grid>
              <Grid container item xs={12} spacing={3} className={classes.inputContainer}>
                <Grid item xs={4}>
                  <Typography className={classes.labelStyles}>Password</Typography>
                </Grid>
                <Grid item xs={8}>
                  <TextField
                    type="password"
                    variant="standard"
                    label="Password"
                    className={classes.inputStyles}
                    value={password}
                    fullWidth
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </Grid>
              </Grid>
              <Button
                variant="contained"
                color="secondary"
                style={{ margin: '60px auto', borderRadius: '50px', padding: '12px 20px' }}
                onClick={onSignIn}
              >
                Login
              </Button>
            </Grid>
          </TabPanel>
          <TabPanel value={value} index={1} style={{ backgroundColor: 'rgba(255,255,255,0.5)' }}>
            <Grid container spacing={1}>
              <Grid container item xs={12} spacing={3} className={classes.inputContainer}>
                <Grid item xs={4}>
                  <Typography className={classes.labelStyles}>Email</Typography>
                </Grid>
                <Grid item xs={8}>
                  <TextField
                    type="email"
                    variant="standard"
                    label="Email"
                    className={classes.inputStyles}
                    value={newEmail}
                    fullWidth
                    onChange={(e) => setNewEmail(e.target.value)}
                  />
                </Grid>
              </Grid>
              <Grid container item xs={12} spacing={3} className={classes.inputContainer}>
                <Grid item xs={4}>
                  <Typography className={classes.labelStyles}>Password</Typography>
                </Grid>
                <Grid item xs={8}>
                  <TextField
                    type="password"
                    variant="standard"
                    label="Password"
                    className={classes.inputStyles}
                    value={newPassword}
                    fullWidth
                    onChange={(e) => setNewPassword(e.target.value)}
                  />
                </Grid>
              </Grid>
              <Grid container item xs={12} spacing={3} className={classes.inputContainer}>
                <Grid item xs={4}>
                  <Typography className={classes.labelStyles}>Admin Password</Typography>
                </Grid>
                <Grid item xs={8}>
                  <TextField
                    type="password"
                    variant="standard"
                    label="Admin Password"
                    className={classes.inputStyles}
                    value={adminPassword}
                    fullWidth
                    onChange={(e) => setAdminPassword(e.target.value)}
                  />
                </Grid>
              </Grid>
              <Button
                variant="contained"
                color="secondary"
                style={{ margin: '60px auto', borderRadius: '50px', padding: '12px 20px' }}
                onClick={onSignUp}
              >
                Create Account
              </Button>
            </Grid>
          </TabPanel>
        </Container>
      </CssBaseline>
    </ThemeProvider>
  );
}
